import Home from './pages/Home.vue'
import Login from './pages/SignIn.vue'
import Forget from './pages/Forget.vue'

export default function (router) {
  router.map({
    '/': {
      name: 'home',
      component: Home
    },
    '/login': {
      name: 'login',
      component: Login
    },
    '/forget': {
      name: 'forget',
      component: Forget
    },
    '/user': {
      name: 'user',
      component: function (resolve) {
        require(['./pages/User.vue'], resolve)
      }
    }
  })
  router.redirect({
    '*': '/'
  })
}



