import * as utils from '../utils'

export const fullscreen = ({ dispatch }) => {
  dispatch('TOGGLE_FULLSCREEN')
  utils.fullscreen()
}

export const login = ({dispatch}, token) => dispatch('LOGIN', token)
export const logout = ({ dispatch }) => dispatch('LOGOUT')



