import Vue from 'vue'
import Vuex from 'vuex'
import fullscreen from './modules/fullscreen'
import auth from './modules/auth'
Vue.use(Vuex)

Vue.config.debug = true

const debug = process.env.NODE_ENV !== 'production'

export default new Vuex.Store({
  modules: {
    fullscreen,
    auth
  },
  strict: debug
})



