<template>
  <div id="box" class="app-sidebar-fixed app-navbar-fixed ">
    <!-- toaster directive -->
    <!-- <toaster-container toaster-options="{'position-class': 'toast-top-right', 'close-button':true}"></toaster-container> -->
    <!-- / toaster directive -->
    <div v-if="showTopBar">
      <sidebar></sidebar>
      <div class="app-content">
        <!-- top navbar -->
        <top-navbar></top-navbar>
        <!-- / top navbar -->
        <!-- main content -->
        <div  class="main-content">
          <div class="wrap-content container">
            <router-view></router-view>
          </div>
        </div>
        <!-- / main content -->
      </div>
      <!-- footer -->
      <footer data-ng-include=" 'assets/views/partials/footer.html' " class="hidden-print"></footer>
      <!-- / footer -->
      <chatbox api="http://demo1429768.mockable.io/messages"></chatbox>
    </div>
    <div v-else>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  import sidebar from './layout/Sidebar.vue'
  import topNavbar from './layout/TopNavbar.vue'
  import chatbox from './layout/Chatbox.vue'
  import fullscreen from './components/Fullscreen'
  import store from './vuex/store'

  export default {
    computed: {
      // 从vuex中获取登陆状态
      checkAuth () {
        return store.state.auth.isAuthed
      },
      // 是否显示顶部的导航条
      showTopBar () {
        if (this.$route.path === '/') {
          return false
        } else {
          return this.checkAuth
        }
      }
    },
    store: store,
    ready () {
      if (!this.checkAuth && this.$route.name !== 'forget') {
        this.$route.router.go('/login')
      }
    },
    components: {
      sidebar,
      topNavbar,
      chatbox,
      fullscreen
    }
  }
</script>

<style lang="scss">
  @import "~src/sass/bases";
</style>



