import Vue from 'vue'

import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
import registerRouters from './routers'
import App from './App.vue'
import { sync } from 'vuex-router-sync'
import store from './vuex/store'

Vue.use(VueResource)
Vue.use(VueRouter)
Vue.http.options.root = 'http://w.stario.net/api'
// Vue.http.options.root = 'http://my.app/api'

// 如果token过期，去后台刷新
Vue.http.interceptors.push({
  request: function (request) {
    var token = window.localStorage.getItem('wemesh_token')
    Vue.http.headers.common['Authorization'] = 'Bearer ' + token
    return request
  },
  response: function (response) {
    if (response.status === 401) {
      this.$http.post('auth/refresh').then(function (response) {
        var token = response.data.result
        window.localStorage.setItem('wemesh_token', token)
        Vue.http.headers.common['Authorization'] = 'Bearer ' + token
        window.location.reload()
      }, function (response) {
        window.console.log('err')
      })
    }
    return response
  }
})

const router = new VueRouter({
  hashbang: false,
  history: true,
  saveScrollPosition: true
})

sync(store, router)

registerRouters(router)

router.start(App, '#app')
// require('./api').register(router.app)



