<template>
  <div class="overlay vcenter">
    <div class="overlay_mask"></div>
    <div class="overlay_content">
      <slot name="overlay_hide"></slot>
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    show: {
      twoWay: true
    }
  },
  methods: {
    open () {
      var el = document.querySelector('html')
      el.classList.add('overlay_open')
      document.addEventListener('touchmove', disableScroll, false)
    },
    close () {
      var el = document.querySelector('html')
      el.classList.remove('overlay_open')
      document.removeEventListener('touchmove', disableScroll, false)
    }
  },
  ready () {
    this.open()
    if (this.show) {
      this.open()
    }
  },
  detached () {
    this.close()
  },
  watch: {
    'show': function (val) {
      if (val) {
        this.open()
      } else {
        this.close()
      }
    }
  }
}

function disableScroll (e) {
  e.preventDefault()
}
</script>

<style lang="scss">
  @import "~src/sass/variables";

.overlay {
  position: fixed;
  display: none;
  justify-content: center;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: url(http://7lrzqf.com1.z0.glb.clouddn.com/image/cropped-staraw2.jpg);
    background-size: cover;
    background-attachment: fixed;
    background-position: top center;
  background-color: #4c5766;
  z-index: 9999;
}
.overlay_open,
.overlay_open body {
  overflow: hidden;
}
.overlay_open .overlay {
  display: flex;
}
.overlay_mask {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background: rgba(0, 5, 8, 0.9); 
}
.overlay_content {
  position: relative;
  box-sizing: border-box;
  padding-left: 10px;
  padding-right: 10px;
  z-index: 22;
  flex: 1;
}
/* slot=overlay-hide is an extra part to hide overlay */
.overlay [slot="overlay_hide"] {
  position: absolute;
}
</style>



