<template>
     <overlay>
      <div class="container">
    <div class="row">
      <div class="col-md-6 col-sm-6 col-xs-12 col-lg-4 col-md-offset-3 col-lg-offset-4 col-sm-offset-3 header padding-top-20 center  login">
        <img src="http://7lrzqf.com1.z0.glb.clouddn.com/images/ucenter-logo.png">
        <h5>微脉事 WeMesh&trade;</h5>
        <!-- 绑定公众号 -->
         <div v-show="!isBind" transition="fade" class="col-md-12  margin-bottom-40">
          <div class="panel">
          <div class="panel-heading">
            <h3><img src="http://7xsyke.com2.z0.glb.clouddn.com/images/icon32_appwx_logo.png" alt="wechat_logo"> 绑定您的公众号</h3>
          </div>
            <div class="panel-body" style="text-align:left">
                  <ul>
                    <li><strong>微脉事WeMesh&trade;</strong>专为微信公众平台而生，功能更加丰富，工作更加高效</li>
                    <li>授权登录<strong>不会对您的公众号造成任何影响</strong></li>
                    <li>您需要作为公众号的管理员完成下面的操作，请<strong>点击下面的绿色按钮</strong></li>
                  </ul>
            </div>
          </div>
          <a v-bind:href="info.url"> <img src="http://7xsyke.com2.z0.glb.clouddn.com/images/icon_button3_1.png"></a>
        </div>
                <!-- 已绑定公众号列表 -->
         <div v-show="isBind" transition="fade" class="col-md-12  margin-bottom-40">
          <div class="panel">
          <div class="panel-heading">
            <h3><img width="32" src="http://7xsyke.com2.z0.glb.clouddn.com/images/icon64_wx_logo.png" alt=""> 您已绑定的公众号</h3>
          </div>
            <div class="panel-body" style="text-align:left">
                  <ul>
                    <li v-for="mp in info.mps"> {{mp}}</li>
                  </ul>
            </div>
          </div>
          <a v-bind:href="info.url" class="btn btn-success btn-lg"><i class="fa fa-wechat"></i> 绑定新的公众号</a>
        </div>
    </div>
  </div>
</div>
  </overlay>
</template>

<script>
import overlay from '../components/Overlay.vue'
import forgetForm from '../components/ForgetForm.vue'
import * as utils from '../utils'
export default {
  data () {
    return {
      isBind: false,
      info: {},
      'error': '',
      'showErr': ''
    }
  },
  route: {
    data (transition) {
      this.$http.get('admin/home').then(function (request) {
        window.console.log(request.data.url)
        this.info = request.data
        this.isBind = request.data['isBind']
      }, function (response) {
        this.$set('error', utils.findAll(response.data))
        this.$set('showErr', true)
      })
    }
  },
  components: {
    overlay,
    forgetForm
  }
}
</script>
<style lang="scss">
  @import "~src/sass/panel";
  ul{
    padding-left:10px;
  }
  li{
    line-height:24px;
    padding:5px 0 5px 5px;
  }
</style>


