<template>
  <div>
    <div id="off-sidebar"  class="sidebar hidden-print">
      <div class="sidebar-wrapper">
        <ul class="nav nav-tabs nav-justified" role="tablist">
          <li role="presentation" class="active">
            <a href="#messages" role="tab" data-toggle="tab"><i class="fa fa-comments"></i></a>
          </li>
          <li role="presentation">
            <a href="#users" role="tab" data-toggle="tab"><i class="fa fa-users"></i></a>
          </li>
          <li role="presentation">
            <a href="#setting" role="tab" data-toggle="tab"><i class="fa fa-cogs"></i></a>
          </li>
        </ul>
        <div class="tab-content">
          <div role="tabpanel" class="tab-pane active" id="messages">
            <div id="users">
            <div class="users-list">
              <div class="sidebar-content" id="chatbox">
                <h5 class="sidebar-title">微信留言</h5>
                <ul class="media-list">
                  <li v-for="item in items" class="media">
                    <a v-on:click="checkChat">
                      <i class="status-online">3天前</i>
                      <img :src="item.avatar" class="media-object" alt="">
                      <div class="media-body">
                        <h4 class="media-heading">{{item.from}}</h4>
                        <span>普通用户</span>
                      </div>
                    </a>
                  </li>
                </ul>
              </div>
            </div>
            <div class="user-chat">
              <chat-clip></chat-clip>
            </div>
            </div>
          </div>
          <div role="tabpanel" class="tab-pane" id="users">
            asdfasdfasdf
          </div>
          <div role="tabpanel" class="tab-pane" id="setting">
            3333333
          </div>
        </div>
      </div>
    </div>

</template>

<script>
  var Ps = require('perfect-scrollbar')
  import chatClip from '../components/ChatClip.vue'
  import * as utils from '../utils'
  export default{
    props: ['api'],
    data: function () {
      return {
        items: {}
      }
    },
    ready: function () {
      this.$http.get(this.api).then(function (response) {
        this.items = response.data
      })
      var container = document.getElementById('chatbox')
      Ps.initialize(container)
    },
    methods: {
      checkChat: function () {
        var el = document.getElementById('users')
        utils.addClass(el, 'chat-open')
      }
    },
    components: {
      chatClip
    }
  }
</script>

