    @media (min-width: $screen-md-min) {
      width: $sideBar-width - 40;

      .navbar-brand.navbar-brand-collapsed {
        padding: 0;
        width: $sideBar-closed-width - 20 !important;
      }
    }
  }

  .navbar-collapse {
    @media (min-width: $screen-md-min) {
      margin-left: $sideBar-width - 40;
    }
  }




}

</style>


